[role="banner"] {
    // display: none !important;
    h1[aria-hidden="true"] {
        display: none;
    }
    .ie-browser-msg {
        width: 100%;
        padding: 1em 10%;
        background: var(--hgob);
        color: var(--grayDk);
        text-align: center; // triggers in all-rev2022
    }
    [id="csus-global-navbar"] {
        padding: 2rem 0 1rem;
        .container {
            max-width: 1400px; // overrides _grid ln 18
            padding: 0; // overrides bootsrap 15px
            flex-wrap: nowrap; // ============================= Main Logo :: Top Nav 
            .navbar-brand {
                padding: 0;
                margin: 0 0 0 5%;
                flex: 0 0 55%;
                @media screen and (min-width: 640px) {
                    flex: 0 0 50%;
                }
                @media screen and (min-width: 768px) {
                    flex: 0 0 35%;
                }
                img {
                    width: 45%; // min-width: 240px;
                    max-width: 100%;
                    @media screen and (max-width: 960px) {
                        width: 100%;
                        -o-object-fit: contain;
                        object-fit: contain;
                    }
                }
            } // ======================= 3 items list
            ul.nav {
                display: none;
                @media screen and (min-width: 960px) {
                    display: flex;
                    flex: 1;
                    justify-content: flex-end;
                    li {
                        a {
                            font-size: 1.25rem;
                            font-weight: 600;
                            display: block;
                            padding: 0.5rem 1rem;
                            color: var(--black);
                            text-decoration: none;
                            &:hover,
                            &:focus {
                                text-decoration: underline;
                            }
                        }
                    }
                }
            }
            .navbar-toggler {
                flex: 0 0 35%;
                text-align: left;
                background-image: url("../svg/search.svg"), url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(4,57,39, 1)' stroke-width='4' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E");
                background-position: 80% 50%, 25% 50%;
                background-size: contain, contain;
                font-weight: 600;
                text-indent: -9999px;
                text-transform: uppercase;
                border: 0;
                padding: 0;
                @media screen and (min-width: 420px) {
                    background-position: 90% 50%, 65% 50%;
                    flex: 0 0 40%;
                    padding: 0 20% 0 0;
                }
                @media screen and (min-width: 640px) {
                    background-position: 90% 50%, 70% 50%;
                    flex: 0 0 45%;
                    text-indent: 0;
                    text-align: right;
                }
                @media screen and (min-width: 960px) {
                    background-position: 65% 50%, 20% 50%;
                    flex: 0 0 12%;
                    padding-right: 10%;
                    text-indent: -9999px;
                    text-align: unset;
                }
                @media screen and (min-width: 1400px) {
                    background-position: 100% 50%, 65% 50%;
                }
            }
            [id="quicklinks-modal"] {
                //                &.fade {
                //                    &.modal {
                &.show {
                    // transform: translate(5%, 0)scale(1);
                }
                .modal-dialog {
                    min-height: 100%;
                    margin: 0 0 0 auto;
                    background: transparent;
                    .modal-content {
                        width: 90%;
                        border: 0;
                        margin: 0 0 0 5%;
                        background: transparent;
                        .modal-header {
                            border-bottom: 0;
                            position: relative;
                            h3 {}
                            .close {
                                background: var(--rwgr);
                                margin: 0;
                                color: var(--white); //text-shadow: none;
                                font-size: .875rem;
                                padding: .5rem .75rem;
                                position: absolute;
                                top: 2rem;
                                right: calc(100% - 1rem);
                                z-index: 10;
                                border-radius: 50%; // box-shadow: rgba(0, 0, 0, .3) 0 3px 6px;
                            }
                        }
                        .modal-body {
                            overflow-y: auto;
                            overflow-x: hidden;
                            padding: 0;
                            border-top-left-radius: 4px;
                            border-bottom-left-radius: 4px;
                            box-shadow: rgba(50, 50, 50, .5) 112px 24px 36px;
                            ul {
                                list-style: none;
                            }
                            a {
                                color: #000;
                                &:hover,
                                &:focus {
                                    text-decoration: none;
                                }
                            }
                            form[role="search"] {
                                // min-height: unset;
                                position: relative;
                                margin-top: 1rem;
                                width: 100%;
                                padding: 1em 5%;
                                display: flex;
                                align-items: center;
                                background-color: var(--white);
                                border-top-right-radius: 4px;
                                fieldset {
                                    display: flex;
                                    flex: 1;
                                    position: relative;
                                    legend {
                                        /* hide search label */
                                        text-indent: -9999px;
                                        max-height: 1px;
                                        margin: 0;
                                    }
                                    input.form-control {
                                        border: 3px solid var(--hgob);
                                        border-radius: 4px;
                                        min-height: 2.5rem;
                                        padding: .5rem;
                                        flex: 1;
                                        width: calc(100% - 30px);
                                        &:focus {}
                                    }
                                    button {
                                        //                                        position: absolute;
                                        //                                        z-index: 1;
                                        //                                        top: 5px;
                                        //                                        right: 20px;
                                        background-color: var(--white);
                                        width: 44px;
                                        border: none;
                                        cursor: pointer;
                                        display: flex;
                                        justify-content: flex-end;
                                        align-items: center; //                                        padding: 0;
                                        //                                        position: absolute;
                                        //                                        top: 35px;
                                        //                                        right: 4px;
                                        //                                        background: #fff;
                                        //                                        border: 0;
                                        //                                        top: 4px;
                                        //                                        right: 6px;
                                        &:hover,
                                        &:focus {
                                            background-color: var(--black40a);
                                        }
                                        span {
                                            //display: none;
                                            // padding-left: 1em;
                                            /* hide search label */
                                            text-indent: -9999px; //max-height: 1px;
                                            margin: 0;
                                        }
                                    }
                                }
                            }
                            [id="top-quick-links"].ql-stacker {
                                min-height: unset;
                                background-color: #f5de95;
                                display: flex;
                                flex-wrap: wrap;
                                padding: 0;
                                text-align: center;
                                width: 100%;
                                margin: 0;
                                list-style: none;
                                @media screen and (min-width: 960px) {
                                    display: flex;
                                    margin-right: 0;
                                    flex: 0 0 50%;
                                }
                                @media screen and (min-width: 1000px) {
                                    flex: 0 0 55%;
                                }
                                li {
                                    min-height: 90px;
                                    flex: 0 1 100%;
                                    border-bottom: 2px solid #f1cc4d;
                                    min-height: unset;
                                    &:last-child {
                                        border-bottom: 0;
                                    }
                                    &:nth-child(odd) {
                                        border-right: 2px solid #fff;
                                    }
                                    &:nth-child(odd) {
                                        border-right: 0;
                                    }
                                    a {
                                        font-size: 16px;
                                        font-weight: 600;
                                        text-align: left;
                                        background-image: none !important;
                                        border-left: 8px solid #e6b711;
                                        padding: .75em 8%;
                                        text-decoration: none;
                                        &:link,
                                        &:visited {
                                            background-color: var(--black00a);
                                            display: block;
                                        }
                                        &:hover,
                                        &:focus {
                                            background-color: var(--black20a);
                                        }
                                        @media screen and (min-width: 960px) {
                                            &:focus,
                                            &:hover {
                                                text-decoration: none;
                                                color: var(--brzdk);
                                            }
                                        }
                                        @media screen and (min-width: 1000px) {
                                            padding-top: .6em;
                                        }
                                    }
                                }
                            }
                            [id="nav-accordion"] {
                                .card {
                                    border: none; // min-width: 320px;
                                    border-radius: 0;
                                    @media screen and (max-height: 420px) {
                                        display: none;
                                    }
                                    &.mobile-nav-only {
                                        @media screen and (min-width: 960px) {
                                            display: none;
                                        }
                                    }
                                    .collapse.show {
                                        border-bottom: solid 4px var(--brzdk);
                                    }
                                    .card-header {
                                        padding: 0;
                                        background-color: #fff;
                                        border: none;
                                        border-radius: 0;
                                        display: flex;
                                        margin: 0;
                                        &:hover {
                                            background-color: var(--wgolt);
                                        }
                                        > a {
                                            flex: 1;
                                            padding: .75em 10%;
                                            border: none;
                                            border-radius: 0;
                                            border-bottom: 2px solid var(--stgr);
                                            text-decoration: none;
                                            &[aria-expanded="true"] {
                                                border: none;
                                                border-top: 2px solid var(--hgob);
                                                background-color: var(--wgo40a);
                                            }
                                        }
                                    }
                                    [id^="accordion-"] {
                                        &.collapse {}
                                        .card-body {
                                            background-color: var(--hgo20a);
                                            padding: 0;
                                            ul,
                                            li {
                                                display: block;
                                                padding: 0;
                                                margin: 0;
                                            }
                                            li {
                                                border-bottom: 1px solid #f0e1b0;
                                                a {
                                                    display: block;
                                                    padding: .5rem 10%;
                                                    background: var(--black00a);
                                                    font-size: .875rem;
                                                    &:hover {
                                                        background: var(--wgo20a);
                                                    }
                                                }
                                                &:last-of-type {
                                                    border-bottom: 0;
                                                }
                                            }
                                        }
                                    }
                                }
                                [id="social-quick-links"] {
                                    background-color: #004e38;
                                    padding: 0 5%;
                                    margin: 0;
                                    width: 100%;
                                    display: -webkit-inline-box;
                                    display: -ms-inline-flexbox;
                                    display: inline-flex;
                                    border-bottom-left-radius: 4px;
                                    border-bottom-right-radius: 4px;
                                    bottom: 0;
                                    li {
                                        width: 20%;
                                        position: relative;
                                        max-height: 60px;
                                        overflow: hidden;
                                        @media screen and (min-width: 420px) {
                                            max-height: 80px;
                                        }
                                        @media screen and (min-width: 576px) {
                                            max-height: 60px;
                                        }
                                    }
                                }
                            }
                        }
                    } // ???? does this nest under show??
                    //                    -webkit-transform: translate(0,
                    //                    0);
                    //                    transform: translate(0,
                    //                    0);
                    //                    display: flex;
                    //                    height: 100%;
                    //                    min-height: 100vh;
                    //                    align-items: flex-start;
                } //                        }
                //                    }
                //                }
            } // end modal
        } // container
    } // #global-navbar
} // #banner
// ====== end header ======================
