// Global Footer Section  ============
[id="csus-global-footer"] {
    background-color: var(--black);
    padding-top: 2rem;
    @media screen and (min-width: 660px) {
        padding-top: 1rem;
    }
    .container-fluid {
        padding: 0;
        .row {
            width: 90%;
            max-width: 1400px;
            margin: 0 auto;
            -webkit-box-pack: justify;
            -ms-flex-pack: justify;
            justify-content: space-between;
            padding-bottom: 2em;
            @media screen and (min-width: 1000px) {
                flex-wrap: nowrap;
            }
            a {
                color: #f0e1b0;
                text-decoration: underline;
                &:hover {
                    text-decoration: none;
                }
                &[title*="Sac State Home Page"] {
                    display: block;
                    width: 90%;
                    margin: 0 auto 1rem auto;
                    text-align: center;
                    img {
                        -o-object-fit: scale-down;
                        object-fit: scale-down;
                        width: 100%;
                        max-width: 300px;
                    }
                    @media screen and (min-width: 660px) {
                        #csus-global-footer a[title*="Sac State Home Page"] {
                            width: 50%;
                            text-align: left;
                        }
                    }
                    @media screen and (min-width: 768px) {
                        flex: 0 0 50%;
                        max-width: 50%;
                        align-self: center;
                        margin: 0;
                    }
                    @media screen and (min-width: 1000px) {
                        justify-content: flex-end;
                        display: flex;
                        flex: 0 0 30%;
                        max-width: 30%;
                        margin: .5em 0;
                    }
                }
            }
            ul {
                &[aria-labelledby="campus-social-links"] {
                    display: flex;
                    justify-content: space-between;
                    padding: 1em 0;
                    margin: 0 auto;
                    max-width: 320px;
                    order: 1;
                    @media screen and (min-width: 660px) {
                        padding: 0 0 2em 0;
                    }
                    @media screen and (min-width: 768px) {
                        flex: 0 0 50%;
                        max-width: 50%;
                        align-self: center;
                    }
                    @media screen and (min-width: 1000px) {
                        flex: 0 0 30%;
                        max-width: 30%;
                        margin: 0 auto;
                    } //                    @media screen and (min-width: 1200px) {
                    //                        -webkit-box-flex: 0;
                    //                        -ms-flex: 0 0 40%;
                    //                        flex: 0 0 40%;
                    //                        max-width: 40%;
                    //                        padding: 0 3%;
                    //                    }
                }
                li {
                    height: 51px;
                    width: 62px;
                    flex: 0 1 15%;
                    justify-items: center;
                    margin: 0;
                    position: relative;
                }
            }
            address {
                font-size: .9rem;
                padding: 0;
                max-width: 1400px;
                width: 80%;
                margin: 0 auto;
                text-align: center;
                @media screen and (max-width: 600px) {
                    .locality:before, .phone:before {
                        content: "";
                        display: block;
                    }
                    .country-name {
                        display: none;
                    }
                }
                @media screen and (min-width: 660px) {
                    font-size: .75em;
                    padding: 1.5rem 5%;
                    width: 50%;
                    text-align: left;
                }
                @media screen and (min-width: 768px) {
                    flex: 0 0 50%;
                    max-width: 50%;
                    align-self: center;
                    margin: 0 0 0 50%;
                    padding: 0;
                    font-size: .9em;
                }
                @media screen and (min-width: 1000px) {
                    flex: 0 0 30%;
                    max-width: 30%;
                    margin: .5em 0;
                }
            }
        }
    }
    .bottombar {
        background-color: var(--rwgr);
        position: relative;
        ul {
            padding: 1rem 0;
            max-width: 1400px;
            width: 90%;
            margin: 0 auto;
            text-align: center;
            display: flex;
            flex-wrap: wrap;
            list-style: none;
            justify-content: center;
            margin: 0 auto;
            li {
                white-space: nowrap;
                hyphens: none;
                a {
                    font-size: .875rem;
                    display: block;
                    color: var(--wgolt);
                    padding: 0 3ch;
                    text-decoration: none;
                    &:hover {
                        color: var(--white);
                        text-decoration: underline;
                    }
                }
            }
        }
    }
} // ============ Icon Inserts
#csus-global-footer,
#csus-global-navbar {
    ul[aria-labelledby="campus-social-links"],
    ul[id="social-quick-links"] {
        li {
            a {
                color: #FFF;
                font-size: 0;
                text-align: center;
                text-decoration: none;
                &:before {
                    position: relative;
                    /*top: -.3rem;*/
                    text-decoration: none;
                }
                &[href*="twitter.com"]:before {
                    content: url(../svg/socials/tw.svg);
                }
                &[href*="facebook.com"]:before {
                    content: url(../svg/socials/fb.svg);
                }
                &[href*="instagram.com"]:before {
                    content: url(../svg/socials/ig.svg);
                }
                &[href*="flickr.com"]:before {
                    content: url(../svg/socials/fk.svg);
                }
                &[href*="youtube.com"]:before {
                    content: url(../svg/socials/yt.svg);
                }
                &[href*="linkedin.com"]:before {
                    content: url(../svg/socials/li.svg);
                }
                &:hover,
                &:focus {
                    opacity: .6;
                }
            }
        }
    }
}

// Global Utility UI
.button-copy-anchor {
    text-indent: -2px;
    white-space: nowrap;
    overflow: hidden;
    /*
    font-size: 20px;
    width: 20px;
    height: 20px;
    background: none;*/
    border: none;
    color: #00573d;
}

a.skip-nav {
    position: absolute;
    left: -9999px;
    width: 1px;
    height: 1px;
    overflow: hidden;
    display: block;
    &:focus {
        position: relative;
        left: 1rem;
        top: 1rem;
        border-radius: 4px;
        width: auto;
        height: auto;
        overflow: unset;
        text-align: center;
        background: var(--wgolt);
        color: var(--rwgr);
        padding: .5rem 1rem;
    }
}

[id="button-top"] {
    display: none;
    position: fixed;
    bottom: 1.5rem;
    right: 1.5rem;
    z-index: 99;
    border: none;
    background-color: var(--ssgr);
    color: var(--white);
    cursor: pointer;
    padding: .625rem;
    border-radius: 4px;
    font-size: .875rem;
    line-height: 1; //width: 40%;
    text-shadow: 0;
    @media screen and (min-width: 1000px) {
        bottom: 4rem;
    }
    &:hover {
        background-color: var(--rwgr); // box-shadow: var(--black60a) 0 3px 0;
    }
}


/**************** Edit "Green Dot" Style ****************/

.dot {
    height: 0.65rem;
    width: 0.65rem;
    background-color: var(--ssgr);
    border-radius: 50%;
    //    margin-bottom: 2px;
    //    margin-left: 1em;
    //    margin-top: 0.5em;
    //    float: left;
    text-indent: 100%;
    white-space: nowrap;
    overflow: hidden;
    -webkit-transition: ease 0.5s;
    /* Safari prior 6.1 */
    transition: ease 0.5s;
    position: absolute;
    top: 50%;
    left: 2%;
    &:hover {
        background-color: #ccc;
    }
    @media screen and (max-width:576px) {
        display: none;
    }
}